/**
 * 
 */
import React from 'react';
import FormItem from '../components/FormItem'; //  ./FormItem
//  formProvider
import formProvider from '../utils/formProvider';
//  prop-types
import PropTypes from 'prop-types';
// 
import AutoComplete from './AutoComplete';

class BookEditor extends React.Component {
  // 
  handleSubmit(e){
    // submit
    e.preventDefault();
    // 
    const { form: { name, price, owner_id }, formValid, editTarget} = this.props; // 
    // 
    if(!formValid){
      alert('');
      return;
    }

    // 
    let editType = '';
    let apiUrl = 'http://localhost:8000/book';
    let method = 'post';
    // 
    if(editTarget){
      editType = '';
      apiUrl += '/' + editTarget.id;
      method = 'put';
    }

    // 
    fetch(apiUrl, {
      method, // method: method 
      // fetchjsonJSON.stringify
      body: JSON.stringify({
        name: name.value,
        price: price.value,
        owner_id: owner_id.value
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    })
    // json
    .then((res) => res.json())
    // 
    .then((res) => {
      // ,jsonid
      // res.id
      if(res.id){
        alert(editType + '!');
        this.context.router.push('/book/list'); // 
        return;
      }else{
        alert(editType + '!');
      }
    })
    // 
    .catch((err) => console.error(err));
  }

  // --
  componentWillMount(){
    const {editTarget, setFormValues} = this.props;
    if(editTarget){
      setFormValues(editTarget);
    }
  }
  
  render() {
    // 
    const {form: {name, price, owner_id}, onFormChange} = this.props;
    return (
      <form onSubmit={(e) => this.handleSubmit(e)}>
        <FormItem label=":" valid={name.valid} error={name.error}>
          <input
            type="text"
            value={name.value}
            onChange={(e) => onFormChange('name', e.target.value)}/>
        </FormItem>

        <FormItem label=":" valid={price.valid} error={price.error}>
          <input
            type="number"
            value={price.value || ''}
            onChange={(e) => onFormChange('price', e.target.value)}/>
        </FormItem>

        <FormItem label=":" valid={owner_id.valid} error={owner_id.error}>
          <AutoComplete
            value={owner_id.value ? owner_id.value + '' : ''}
            options={['10000()','10001()']}
            onValueChange={value => onFormChange('owner_id', value)} />
        </FormItem>
        <br />
        <input type="submit" value="" />
      </form>
    );
  }
}

// BookEditorroutercontextTypes
// this.context.routerReact Router
BookEditor.contextTypes = {
  router: PropTypes.object.isRequired
};

// 
BookEditor = formProvider({ // field 
  // 
  name: {
    defaultValue: '',
    rules: [
      {
        pattern: function (value) {
          return value.length > 0;
        },
        error: ''
      },
      {
        pattern: /^.{1,10}$/,
        error: '10'
      }
    ]
  },
  // 
  price: {
    defaultValue: 0,
    rules: [
      {
        pattern: function(value){
          return value > 0;
        },
        error: '0'
      }
    ]
  },
  // 
  owner_id: {
    defaultValue: '',
    rules: [
      {
        pattern: function (value) {
          return value.length > 0;
        },
        error: ''
      },
      {
        pattern: /^.{1,10}$/,
        error: '10'
      }
    ]
  }
})(BookEditor);

export default BookEditor;


