/**
 * 
 */
import React from 'react';
// 
import HomeLayout from '../layouts/HomeLayout';
//  prop-types
import PropTypes from 'prop-types';

class UserList extends React.Component {
  // 
  constructor(props) {
    super(props);
    // 
    this.state = {
      userList: []
    };
  }

  /**
   * 
   * componentWillMount
   * 
   */
  componentWillMount(){
    // 
    fetch('http://localhost:8000/user')
      .then(res => res.json())
      .then(res => {
        /**
         * 
         * 
         */
        this.setState({
          userList: res
        });
      });
  }

  /**
   * 
   */
  handleEdit(user){
    // 
    this.context.router.push('/user/edit/' + user.id);
  }

  /**
   * 
   */
  handleDel(user){
    // 
    const confirmed = window.confirm(` ${user.name} ?`);
    // 
    if(confirmed){
      // 
      fetch('http://localhost:8000/user/' + user.id, {
        method: 'delete'
      })
      .then(res => res.json())
      .then(res => {
        /**
         * 
         * array.filter
         * Array
         */
        this.setState({
          userList: this.state.userList.filter(item => item.id !== user.id)
        });
        alert('');
      })
      .catch(err => {
        console.log(err);
        alert('');
      });
    }
  }

  render() {
    // 
    const { userList } = this.state;

    return (
      <HomeLayout title="">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>

          <tbody>
            {
              userList.map((user) => {
                return (
                  <tr key={user.id}>
                    <td>{user.id}</td>
                    <td>{user.name}</td>
                    <td>{user.gender}</td>
                    <td>{user.age}</td>
                    <td>
                      <a onClick={() => this.handleEdit(user)}></a>
                      &nbsp;
                      <a onClick={() => this.handleDel(user)}></a>
                    </td>
                  </tr>
                );
              })
            }
          </tbody>
        </table>
      </HomeLayout>
    );
  }
}

/**
 * this.context.xxxcontextTypesPropTypes
 */
UserList.contextTypes = {
  router: PropTypes.object.isRequired
};

export default UserList;


