/**
 * 
 */
import React from 'react';
// 
import HomeLayout from '../layouts/HomeLayout';
//  prop-types
import PropTypes from 'prop-types';

class BookList extends React.Component {
  // 
  constructor(props) {
    super(props);
    // 
    this.state = {
      bookList: []
    };
  }

  /**
   * 
   * componentWillMount
   * 
   */
  componentWillMount(){
    // 
    fetch('http://localhost:8000/book')
      .then(res => res.json())
      .then(res => {
        /**
         * 
         * 
         */
        this.setState({
          bookList: res
        });
      });
  }

  /**
   * 
   */
  handleEdit(book){
    // 
    this.context.router.push('/book/edit/' + book.id);
  }

  /**
   * 
   */
  handleDel(book){
    // 
    const confirmed = window.confirm(` ${book.name} ?`);
    // 
    if(confirmed){
      // 
      fetch('http://localhost:8000/book/' + book.id, {
        method: 'delete'
      })
      .then(res => res.json())
      .then(res => {
        /**
         * 
         * array.filter
         * Array
         */
        this.setState({
          bookList: this.state.bookList.filter(item => item.id !== book.id)
        });
        alert('');
      })
      .catch(err => {
        console.log(err);
        alert('');
      });
    }
  }

  render() {
    // 
    const { bookList } = this.state;

    return (
      <HomeLayout title="">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>

          <tbody>
            {
              bookList.map((book) => {
                return (
                  <tr key={book.id}>
                    <td>{book.id}</td>
                    <td>{book.name}</td>
                    <td>{book.price}</td>
                    <td>
                      <a onClick={() => this.handleEdit(book)}></a>
                      &nbsp;
                      <a onClick={() => this.handleDel(book)}></a>
                    </td>
                  </tr>
                );
              })
            }
          </tbody>
        </table>
      </HomeLayout>
    );
  }
}

/**
 * this.context.xxxcontextTypesPropTypes
 */
BookList.contextTypes = {
  router: PropTypes.object.isRequired
};

export default BookList;


